@import url("https://fonts.googleapis.com/css?family=Lora:400,700|Libre+Barcode+128+Text");
@import url("https://fonts.googleapis.com/css?family=Shadows+Into+Light|Valero|Oswald|Aleo|Lato");
.experience a:visited,
.experience a:link {
  color: white;
  background-color: black;
  border-radius: 0.3rem;
  padding: 3px;
  text-decoration: none;
}
.experience a:hover {
  padding: 5.5px;
  text-decoration: none;
}
.experience button:hover {
  padding: 10px;
}
/* scroll button distance between the info section */
.scrollSection {
  padding-top: 30px;
}
.scrollSection p {
  color: black;
  font-size: 20px;
}
/* scroll button item */
#scrollButton span {
  position: absolute;
  padding-top: 30px;
  left: 50%;
  width: 30px;
  height: 50px;
  margin-left: -15px;
  border: 2px solid black;
  border-radius: 50px;
  box-sizing: border-box;
  -webkit-animation: sdb03 4s infinite;
  animation: sdb03 4s infinite;
}
/* set the dot in the middle of the icon to 10px from top */
#scrollButton span::before {
  position: absolute;
  top: 10px;
  left: 50%;
  content: "";
  width: 6px;
  height: 6px;
  margin-left: -3px;
  opacity: 0;
  background-color: black;
  border-radius: 100%;
  box-sizing: border-box;
  -webkit-animation: sdb03 4s infinite;
  animation: sdb03 4s infinite;
}
@-webkit-keyframes sdb03 {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    box-shadow: 0 0 0 60px rgba(255, 255, 255, 0.1);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes sdb03 {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    box-shadow: 0 0 0 60px rgba(255, 255, 255, 0.1);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.aboutMe h1 {
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
  font-weight: 400;
}
.aboutMe {
  background: white;
  color: black;
  padding: 10vh;
  font-size: 1em;
}
.skill h3 {
  font-family: "Shadows Into Light", cursive;
  font-size: 4rem;
  color: #766f57;
}
p {
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
  font-weight: 100;
}
.td {
  padding-left: 0px !important;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.experience h3,
.newExperience {
  margin-top: 10vh;
}

.experience h6,
.experience h5,
.experience ul {
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
}
.experience h6 {
  font-weight: 300;
}
.experience ul {
  font-weight: 300;
}
.navbar {
  height: 6vh;
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
}
.navbar a:hover {
  padding: 5px;
  text-decoration: underline;
}
.navbar-collapse {
  background: white;
}
.navbarToggler {
  border: none;
}
.footer .link:hover {
  color: #61838d;
}
.txt-type {
  border-right: 0.5rem solid black;
}
.landingPage {
  height: 100vh;
  position: absolute;
  font-family: "Lato", Helvetica, Arial, Lucida, sans-serif;
  color: black;
  max-width: 100vh;
  z-index: 1;
  transition: all 1000ms;
}
.landingPage h1 {
  font-size: 6vh;
  font-weight: 100;
}
.showcase::after {
  content: "";
  background-image: url(../misc/streetLamp.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  filter: blur(2px);
  -webkit-filter: blur(2px);
  transition: all 1000ms;
  background-position: center;
  display: block;
}
.showcase:hover::after {
  filter: blur(0px);
  -webkit-filter: blur(0px);
}
.navbar-light {
  background-color: white;
}

.showcase:hover .landingPage {
  display: none;
}

.navbar-light {
  color: white;
}

.logo {
  height: 80px;
  width: 100px;
}
.scrollspy {
  position: relative;
  overflow-y: scroll;
}

.avatar {
  margin-bottom: 1vh;
}

.bodyClass {
  font-family: "Lora", serif;
}
.skill {
  background: white;
}

.footer {
  background: black;
  color: white;
}

.footer .link {
  font-size: 2.3rem;
  font-family: "Libre Barcode 128 Text", cursive;
  color: white;
  font-weight: bold;
}

.footerIcon {
  height: 64px;
  width: 64px;
}
.aboutMe,
.experience,
.skill {
  padding-bottom: 10vh;
}
/* fix ipad landscape */
@media (min-width: 1143px) {
  .avatar {
    height: 20%;
    width: 35%;
  }
}
@media (max-width: 1142px) {
  .avatar {
    height: 300px;
    width: 250px;
  }
}
@media (max-width: 768px) {
  .avatar {
    height: 180px;
    width: 180px;
  }
  .navbar {
    height: 8vh;
  }
}
@media (max-width: 375px) {
  .avatar {
    height: 200px;
    width: 180px;
  }
  .navbar {
    height: 9vh;
  }
}
@media (max-width: 375px) and (min-width: 812px) {
  .avatar {
    height: 200px;
    width: 180px;
  }
  .navbar {
    height: 9vh;
  }
}
@media (max-width: 320px) and (max-height: 600px) {
  .avatar {
    height: 200px;
    width: 170px;
  }
  .footer .link {
    font-size: 2rem;
  }
}
